import React, { Component } from 'react'

import Select from 'react-select'

const commisionOptions = [
  { value: 0, label: "0%"},
  { value: 10, label: "10%"},
  { value: 20, label: "20%"},
  { value: 30, label: "30%"},
  { value: 40, label: "40%"},
  { value: 50, label: "50%"},
  { value: 60, label: "60%"},
  { value: 70, label: "70%"},
  { value: 80, label: "80%"},
  { value: 90, label: "90%"},
]

const commisionOptionsMap = {}

commisionOptions.forEach(o => commisionOptionsMap[o.value.toString()] = o)

const customStyles = {
  control: (provided, state) => ({
    ...provided,
    background: '#fff',
    borderColor: '#9e9e9e',
    minHeight: '20px',
    height: '20px',
    boxShadow: state.isFocused ? null : null,
  }),

  valueContainer: (provided, state) => ({
    ...provided,
    height: '20px',
    padding: '0 6px'
  }),

  input: (provided, state) => ({
    ...provided,
    margin: '0px',
  }),

  indicatorSeparator: state => ({
    display: 'none',
  }),

  indicatorsContainer: (provided, state) => ({
    ...provided,
    height: '20px',
  }),
}

export default class CommisionSetting extends Component {

  handleChange = selectedOption => {
    this.props.setCommision(selectedOption.value)
  }

  render() {
    const { commision } = this.props
    const selectedOption = commisionOptionsMap[commision.toString()] || null

    return <Select
      styles={customStyles}
      placeholder={""+commision+"%"}
      width={25}
      value={selectedOption}
      onChange={this.handleChange}
      options={commisionOptions}
    />
  }
}
