
const hexRegex = /0x[a-fA-F0-9]+/

export function verifyHex(hex: string) {
    return hexRegex.test(hex)
}

export function verifyHex256(hex: string) {
    return hex.length === 66 && hexRegex.test(hex)
}

export function verifyAddress(address: string) {
    return address.length === 42 && verifyHex(address)
}
