import React, { useState } from 'react'

import css from "./IconButton.module.css"

export interface IconButtonProps {
    url?: string;
    children: JSX.Element;
}

const IconButton: React.FC<IconButtonProps> = ({url, children}) => {
    const [hover, setHover] = useState(false);
    const [down, setDown] = useState(false);
    const { button, buttonHover, buttonDown } = css
    const className = down ? buttonDown : hover ? buttonHover : button

    return <span
        onPointerOver={()=> setHover(true)}
        onPointerOut={() => setHover(false)}
        onPointerDown={()=> setDown(true)}
        onPointerUp={() => setDown(false)}
    >
        {url ? <a title={url} href={url} target="_blank" rel="noopener noreferrer">
            <button className={className}>{children}</button>
        </a> : <button className={className}>{children}</button>}
    </span>
}

export default IconButton
