import React from 'react'

import Category from './Category'
import { getDifficultyLabel } from "./Rules"

const second = 1
const minute = 60
const hour = 60 * minute
const day = 24 * hour
const week = 7 * day
const year = 365.25 * day
const century = 100 * year - day
const millenium = 10 * century
const millionYears = 1000*millenium
const billionYears = 1000*millionYears
const trillionYears = 1000*billionYears
const quadYears = 1000*trillionYears

const difficulties = Array.from(Array(11).keys()).map(n => n*10+20)

const timeLabels1 = [ "quad years", "trillion years", "billion years", "million years", "millenium", "century", "year", "week", "day", "hour", "minute", "second" ]
const timeLabels = [ "quad years", "trillion years", "billion years", "million years", "millenia", "centuries", "years", "weeks", "days", "hours", "minutes", "seconds" ]
const timeSpans0: any = { quadYears, trillionYears, billionYears, millionYears, millenium, century, year, week, day, hour, minute, second }
const timeSpans = Object.keys(timeSpans0).map((ts, i) => ({time: timeSpans0[ts], label: timeLabels[i], label1: timeLabels1[i]}))

const getTime = (t: number) => {
  
    const fts = timeSpans.filter(ts => t >= ts.time)

    let s = "0 seconds"

    if(fts.length > 0) {
        const ts = fts[0]
        const n = Math.floor(t / ts.time * 10) / 10
        if(n > 1.0) {
            s = n.toString() + " " + ts.label
        } else {
            s = "1 " + ts.label
        }
    }

    return s
}

const Difficulties = (props: any) => {
    return <div>
        <h2>Difficulties</h2>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <thead>
                <tr>
                    <th title="Difficulty Level" style={{textAlign: "left"}}>Level</th>
                    <th title="Average mining time at hash rate 1GH/s">Avg Mining Time</th>
                </tr>
            </thead>
            <tbody>
                {difficulties
                .map((d, i) => {
                    const label = getDifficultyLabel(d)
                    return <tr key={i}>
                        <td style={{textAlign: "left"}}>{<Category cat={label}>{d} - {label}</Category>}</td>
                        <td style={{textAlign: "right"}}>{getTime(Math.floor(Math.pow(2, d)/Math.pow(10, 9)))}</td>
                    </tr>
                })}
            </tbody>
        </table>
    </div>
}

export default Difficulties
