import React, { useState, useEffect } from 'react'
import { BigNumber } from "@ethersproject/bignumber"
import solve from "./Solve"
import { ZERO } from "../utils/bigNumber"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ProgressBar from "@ramonak/react-progress-bar";
import { CopyToClipboard } from 'react-copy-to-clipboard'

import { getRewardUnits, getWalletDifficultyDiscount, getBalanceDifficultyDiscount, getDifficultyLabel } from "./Rules"
import { MIN_CHALLENGE_DIFFICULTY, MAX_CHALLENGE_DIFFICULTY, CHALLENGE_DIFFICULTY_STEP, TOKEN_DECIMALS, REWARDS_CNT_LIMIT } from "./Parameters"

import Category from "./Category"

export type RegisteredBalancesType = {balances: BigNumber[], blockNumbers: BigNumber[]}
export type ChallengesType = {totalCnt: number, indexes: number[], blockNumbers: BigNumber[], rewardUnits: number[], challengeHashes: string[], nexts: number[], claimsCnt: number[], claimed: boolean[]}
export type ChallengesMapType = {
    blockNumberHex: string,
    challengeHash: string,
    generalDifficulty: number,
    addressDiscount: number,
    balance: BigNumber,
    balanceDiscount: number,
    adjustedDifficulty: number,
    claimsCnt: number,
    claimed: boolean,
}
export type RunStatusType = {
    group: string;
    blockNumberHex: string;
    challengeHash: string;
    trials: number,
    targetDifficulty: number,
    solutionTrials: number,
    solution?: BigNumber,
    solvedDifficulty: number,
    solved: boolean,
    timeMs: number,
    stopFn?: () => void,
}
export type VerifyClaimType = { rewardUnits: number, reason: number, premium: boolean, rewardsCnt: number }

export interface ChallengesProps {
    challenges: ChallengesType;
    group: string;
    address: string;
    registeredBalances: RegisteredBalancesType;
    demo: boolean;
    mobile: boolean;
    verifyClaim: (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => Promise<VerifyClaimType>,
    claimReward: (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => void,
}

function stopAll(runStatus: {[key: string]: RunStatusType}) {
    runStatus && Object.keys(runStatus).forEach(bnStr => {
        const rs = runStatus[bnStr]

        if(rs.stopFn !== undefined) {
            rs.stopFn()
            rs.stopFn = undefined
        }
    })
}

export const challengesRunStatus: {[address: string]: {[key: string]: RunStatusType}} = {}
export const Challenges: React.FC<ChallengesProps> = ({challenges, group, address, registeredBalances, demo, mobile, verifyClaim, claimReward}) => {
    let runStatus = challengesRunStatus[address]
    if(!runStatus) {
        runStatus = {}
        challengesRunStatus[address] = runStatus
    }
    const challengesMap = toChallengesMap(challenges, registeredBalances)

    const [runStatusSnap, setRunStatusSnap] = useState({...runStatus})
    const [currentAddress, setCurrentAddress] = useState("")
    const initChallenges: ChallengesType = {totalCnt: 0, indexes: [], blockNumbers: [], rewardUnits: [], challengeHashes: [], nexts: [], claimsCnt: [], claimed: []}
    const [currentChallenges, setCurrentChallenges] = useState(initChallenges)

    let mounted = true

    const refresh = () => {

        const _refresh = async () => {

            if(mounted) {
                if(currentAddress !== address) {
                    stopAll(challengesRunStatus[currentAddress])

                    setCurrentAddress(address)
                    const nrn: {[key: string]: RunStatusType} = {}
                    Object.keys(runStatus).forEach(bnStr => {
                        const rs = runStatus[bnStr]
                        const nrs = {...rs}
                        nrn[bnStr] = nrs                            
                    })
                    setRunStatusSnap(nrn)
                } else if(currentChallenges !== challenges) {
                    // Reconcile challenges
                    // First, stop and remove the run status for challenges that have been removed
                    // Second, update difficulty in the run status
                    Object.keys(runStatus).forEach(bnStr =>{
                        const rs = runStatus[bnStr]
                        if(rs.group === group) {
                            const cmt = challengesMap[bnStr]
                            if(cmt) {
                                if(rs.targetDifficulty !== cmt.adjustedDifficulty) {
                                    // Update the difficulty
                                    rs.targetDifficulty = cmt.adjustedDifficulty
                                    if(rs.solvedDifficulty >= rs.targetDifficulty) {
                                        // Got it!
                                        rs.solved = true
                                        if(rs.stopFn) {
                                            rs.stopFn()
                                            rs.stopFn = undefined
                                        }
                                    }
                                }
                            } else {
                                // Stop the job
                                if(rs.stopFn) {
                                    rs.stopFn()
                                    rs.stopFn = undefined
                                }
                                delete runStatus[bnStr]
                            }
                        }
                    })
 
                    setCurrentChallenges(challenges)
                    const nrn: {[key: string]: RunStatusType} = {}
                    Object.keys(runStatus).forEach(bnStr => {
                        const rs = runStatus[bnStr]
                        const nrs = {...rs}
                        nrn[bnStr] = nrs                            
                    })
                    setRunStatusSnap(nrn)
                }
            }
        }

        const _cleanup = () => {
            mounted = false
        }

        _refresh()

        return _cleanup
    }

    useEffect(refresh)

    function findBalance(registeredBalances: RegisteredBalancesType, blockNumber: BigNumber) {
        const cnt = registeredBalances.blockNumbers.length
        for(let i = 0; i < cnt; i++) {
            const bn = registeredBalances.blockNumbers[i]
            const bal = registeredBalances.balances[i]
            if(blockNumber.gte(bn)) {
                return bal
            }
        }
        return ZERO
    }

    function toChallengesMap(challenges: ChallengesType, registeredBalances: RegisteredBalancesType) {

        const challengesMap: {[bnStr: string]: ChallengesMapType} = {}
        challenges.blockNumbers.forEach((bn, i) =>{
            const bnStr = bn.toHexString()
            const challengeHash = challenges.challengeHashes[i]
            if(challengesMap[bnStr]) {
                throw new Error("Duplicate block number in challenges: "+bnStr)
            }

            const balance = findBalance(registeredBalances, bn)
            const generalDifficulty = MAX_CHALLENGE_DIFFICULTY(demo) - CHALLENGE_DIFFICULTY_STEP * (challenges.totalCnt - i - 1)
            const addressDiscount = getWalletDifficultyDiscount(challengeHash, address, balance, TOKEN_DECIMALS)
            const balanceDiscount = getBalanceDifficultyDiscount(balance, TOKEN_DECIMALS)
            let adjustedDifficulty = generalDifficulty - addressDiscount - balanceDiscount
            if(adjustedDifficulty < MIN_CHALLENGE_DIFFICULTY(demo)) {
                adjustedDifficulty = MIN_CHALLENGE_DIFFICULTY(demo)
            }

            const claimsCnt = challenges.claimsCnt[i]
            const claimed = challenges.claimed[i]
            const ch = {
                blockNumberHex: bnStr,
                challengeHash,
                generalDifficulty,
                addressDiscount,
                balance,
                balanceDiscount,
                adjustedDifficulty,
                claimsCnt,
                claimed,
            }
            challengesMap[bnStr] = ch
        })
    
        return challengesMap
    }
    
    const handleStartMining = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, blockNumber: BigNumber, challengeHash: string, address: string, difficulty: number) => {
        e.preventDefault()

        const bnStr = blockNumber.toHexString()
        let rs = runStatus[bnStr]

        if(!rs) {
            rs = {
                group,
                blockNumberHex: bnStr,
                challengeHash,
                trials: 0,
                targetDifficulty: difficulty,
                solutionTrials: 0,
                solvedDifficulty: 0,
                solved: false,
                timeMs: 0,
                stopFn: undefined,
            }
            runStatus[bnStr] = rs
        }

        if(rs.stopFn) {
            throw new Error("The solver is already running on block "+bnStr)
        }

        const trials0 = rs.trials
        const timeMs0 = rs.timeMs
        const status = (trials: number, timeMs: number, solution?: BigNumber, solvedDifficulty?: number) => {
            const rs = runStatus[bnStr]
            rs.trials = trials0 + trials
            rs.timeMs = timeMs0 + timeMs

            if(solution) {
                if(solvedDifficulty !== undefined && solvedDifficulty > rs.solvedDifficulty) {
                    rs.solutionTrials = rs.trials
                    rs.solution = solution
                    rs.solvedDifficulty = solvedDifficulty
                    if(rs.solvedDifficulty >= rs.targetDifficulty) {
                        // Got it!
                        rs.solved = true
                        if(rs.stopFn) {
                            rs.stopFn()
                            rs.stopFn = undefined
                        }
                    }
                }
            }

            const nrn = {...runStatus}
            const nrs = {...rs}
            nrn[bnStr] = nrs
            setRunStatusSnap(nrn)
        }

        rs.stopFn = solve(blockNumber, challengeHash, address, rs.solvedDifficulty, status)

        const nrn = {...runStatus}
        nrn[bnStr] = {...rs}
        setRunStatusSnap(nrn)
    }

    const handleStopMining = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, blockNumber: BigNumber) => {
        e.preventDefault()

        const bnStr = blockNumber.toHexString()
        const rs = runStatus[bnStr]
        if(!rs) {
            throw new Error("The solver does not exist "+bnStr)
        }
        if(rs.stopFn) {
            rs.stopFn()
            rs.stopFn = undefined
        }

        const nrn = {...runStatus}
        const nrs = {...rs}
        nrn[bnStr] = nrs
        setRunStatusSnap(nrn)
    }
    
    const handleClaimReward = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, blockNumber: BigNumber) => {
        e.preventDefault()

        const bnStr = blockNumber.toHexString()
        const rs = runStatus[bnStr]
        if(!rs) {
            throw new Error("The solver does not exist "+bnStr)
        }

        if(rs.solution) {
            await claimReward(blockNumber, address, address, rs.solution);
        }

        const nrn = {...runStatus}
        const nrs = {...rs}
        nrn[bnStr] = nrs
        setRunStatusSnap(nrn)
    }
    
    return mobile ?
    <table>
        <thead>
            <tr>
                <th>
                    N
                </th>
                <th>
                    Block
                </th>
                <th style={{textAlign: "left"}}>
                    YOUR<br/>Difficulty
                </th>
                <th>
                    LUMI
                </th>
                <th>
                    Claims
                </th>
                <th>
                    Mining<br/>Progress
                </th>
                <th>
                    Mining<br/>Time
                </th>
                <th>
                    Mine
                </th>
            </tr>
        </thead>
        <tbody>
            {challenges.blockNumbers.map((bn, i) => {
                const challengeHash = challenges.challengeHashes[i]

                const bnStr = bn.toHexString()
                const cmt = challengesMap[bnStr]
                const { adjustedDifficulty, claimsCnt, claimed } = cmt

                const rs = runStatusSnap[bnStr]
                let [ rewardsCnt, rewardUnits ] = getRewardUnits(challengeHash)
                const rewardUnit = challenges.rewardUnits[i]
                const rewardsCntLimit = REWARDS_CNT_LIMIT(demo)
                if(rewardsCnt > rewardsCntLimit) {
                    rewardsCnt = rewardsCntLimit
                }

                const progressTitle = (rs || "") && (rs.solved ? ""+rs.solvedDifficulty : rs.stopFn || rs.trials > 0 ? ""+rs.solvedDifficulty+" / "+adjustedDifficulty : "") 
                const progress = (rs || "") && (rs.solved ? 100 : rs.stopFn || rs.trials > 0? Math.floor(rs.solvedDifficulty * 100 / adjustedDifficulty) : "")

                const startable = !(claimed || (rs && (rs.solved || rs.stopFn)))
                const stopable = rs && rs.stopFn
                const claimable = !claimed && rs && rs.solvedDifficulty >= adjustedDifficulty

                const startFn = (e:any) => handleStartMining(e, bn, challengeHash, address, adjustedDifficulty)
                const stopFn = (e: any) => handleStopMining(e, bn)
                const claimFn = (e: any) => handleClaimReward(e, bn)

                const title = startable ? "Start Mining" : stopable ? "Stop Mining" : claimable ? "Claim" : "Done"
                const disabled = !startable && !stopable && !claimable
                const onClick: any = startable ? startFn : stopable ? stopFn : claimable ? claimFn : null
                const icon = startable ? "play" : stopable ? "pause" : claimable ? "trophy" : "check"
                const style = startable ? {} : stopable ? {} : claimable ? {color: "red"} : {color: "green"}
                return <tr key={i}>
                    <td>{i+1}</td>
                    <th>{bn.toString()}</th>
                    <td style={{textAlign: "left"}}><Category cat={getDifficultyLabel(adjustedDifficulty)}>{adjustedDifficulty}{mobile ? "" : " - "+getDifficultyLabel(adjustedDifficulty)}</Category></td>
                    <td>{rewardUnits*rewardUnit/1000}</td>
                    <td>{claimsCnt}/{rewardsCnt}</td>
                    <td>{progress === "" ? "" : <span title={progressTitle}><ProgressBar width="60px" height="18px" labelSize="10px" labelColor="black" bgColor="#BBDEFB" labelAlignment="center" animateOnRender={false} transitionTimingFunction="linear" transitionDuration="500ms" completed={progress}/></span>}</td>
                    <td>{rs && (rs.timeMs > 0 ? ""+Math.floor(rs.timeMs / 1000).toLocaleString()+"s" : "")}</td>
                    <td>
                        <button title={title} style={style} disabled={disabled} onClick={onClick}><FontAwesomeIcon icon={icon}/></button>
                    </td>
                </tr>
            })}
        </tbody>
    </table>
    : <table>
        <thead>
            <tr>
                <th>
                    N
                </th>
                <th>
                    Block<br/>Number
                </th>
                <th>
                    Challenge Hash
                </th>
                {/* <th>
                    Registered<br/>Balance
                </th>
                <th style={{textAlign: "left"}}>
                    General<br/>Difficulty
                </th>
                <th>
                    Wallet<br/>Discount
                </th>
                <th>
                    Balance<br/>Discount
                </th> */}
                <th>
                    YOUR<br/>Discount
                </th>
                <th style={{textAlign: "left"}}>
                    YOUR<br/>Difficulty
                </th>
                <th>
                    Reward<br/>Tokens
                </th>
                <th>
                    Claims
                </th>
                <th>
                    Status
                </th>
                <th>
                    Mining<br/>Progress
                </th>
                <th>
                    Trials
                </th>
                <th>
                    Time
                </th>
                <th>
                    Mine/Verify/Claim
                </th>
                <th>
                    GoMiner<br/>Command
                </th>
                <th>
                    N
                </th>
            </tr>
        </thead>
        <tbody>
            {challenges.blockNumbers.map((bn, i) => {
                const challengeHash = challenges.challengeHashes[i]
                const hash = challengeHash.substr(0, 8)+"-"+challengeHash.substr(challengeHash.length - 10, 10)

                const bnStr = bn.toHexString()
                const cmt = challengesMap[bnStr]
                // const { generalDifficulty, addressDiscount, balance, balanceDiscount } = cmt
                const { addressDiscount, balanceDiscount } = cmt
                const { adjustedDifficulty, claimsCnt, claimed } = cmt

                const rs = runStatusSnap[bnStr]
                const claimable = !claimed && rs && rs.solvedDifficulty >= adjustedDifficulty
                // const verifiable = !claimed && rs && rs.solvedDifficulty > 0
                let [ rewardsCnt, rewardUnits ] = getRewardUnits(challengeHash)
                const rewardUnit = challenges.rewardUnits[i]
                const rewardsCntLimit = REWARDS_CNT_LIMIT(demo)
                if(rewardsCnt > rewardsCntLimit) {
                    rewardsCnt = rewardsCntLimit
                }

                const statusTitle = claimed ? "Claimed" : rs && (rs.solved ? "Solved (now you need to claim)" : rs.stopFn ? "Mining" : rs.trials > 0 ? "Stopped" : "")
                const statusIcon = claimed ? "check" : (rs || "") && (rs.solved ? "check" : rs.stopFn ? "running" : rs.trials > 0 ? "stop" : "")
                const statusColor = claimed ? "green" : (rs || "") && (rs.solved ? "red" : rs.stopFn ? "red" : rs.trials > 0 ? "red" : "")
                const progressTitle = (rs || "") && (rs.solved ? ""+rs.solvedDifficulty : rs.stopFn || rs.trials > 0 ? ""+rs.solvedDifficulty+" / "+adjustedDifficulty : "") 
                const progress = (rs || "") && (rs.solved ? 100 : rs.stopFn || rs.trials > 0? Math.floor(rs.solvedDifficulty * 100 / adjustedDifficulty) : "")

                const gominer = "./gominer -a "+address+" -b "+bn.toString()+" -c "+challengeHash+" -d "+adjustedDifficulty.toString()+" -t 2"

                return <tr key={i}>
                    <td>{i+1}</td>
                    <th>{bn.toString()}</th>
                    <td><code>{hash}</code></td>
                    {/* <td>{formatBalance(balance, TOKEN_DECIMALS)}</td>
                    <td style={{textAlign: "left"}}>{generalDifficulty} - {getDifficultyLabel(generalDifficulty)}</td>
                    {addressDiscount > 0 ? <td>{addressDiscount}</td> : <td/>}
                    {balanceDiscount > 0 ? <td>{balanceDiscount}</td> : <td/>} */}
                    {addressDiscount > 0 || balanceDiscount > 0 ? <td>{addressDiscount+balanceDiscount}</td> : <td/>}
                    <td style={{textAlign: "left"}}><Category cat={getDifficultyLabel(adjustedDifficulty)}>{adjustedDifficulty} - {getDifficultyLabel(adjustedDifficulty)}</Category></td>
                    <td>{rewardUnits*rewardUnit/1000}</td>
                    <td>{claimsCnt}/{rewardsCnt}</td>
                    <td>{statusIcon && <FontAwesomeIcon title={statusTitle} color={statusColor} icon={statusIcon}/>}</td>
                    <td>{progress === "" ? "" : <span title={progressTitle}><ProgressBar width="60px" height="18px" labelSize="10px" labelColor="black" bgColor="#BBDEFB" labelAlignment="center" animateOnRender={false} transitionTimingFunction="linear" transitionDuration="500ms" completed={progress}/></span>}</td>
                    <td>{rs && (rs.solved ? rs.solutionTrials.toLocaleString() : rs.trials > 0 ? rs.trials.toLocaleString() : "")}</td>
                    <td>{rs && (rs.timeMs > 0 ? ""+Math.floor(rs.timeMs / 1000).toLocaleString()+" s" : "")}</td>
                    <td>
                        <button title="Start Mining" disabled={!!(claimed || (rs && (rs.solved || rs.stopFn)))} onClick={(e) => handleStartMining(e, bn, challengeHash, address, adjustedDifficulty)}><FontAwesomeIcon icon="play"/></button>
                        <button title="Stop Mining" disabled={!(rs && rs.stopFn)} onClick={(e) => handleStopMining(e, bn)}><FontAwesomeIcon icon="pause"/></button>
                        {/* <button disabled={!verifiable} onClick={(e) => handleVerifyClaim(e, bn)}>&#x2714;</button> */}
                        <button title="Claim Your Reward" style={{color: claimable ? "red" : ""}} disabled={!claimable} onClick={(e) => handleClaimReward(e, bn)}><FontAwesomeIcon icon="trophy"/></button>
                    </td>
                    <td>
                        <CopyToClipboard text={gominer}>
                            <button title="Copy GoMiner Command" disabled={claimed}><FontAwesomeIcon icon={["far", "copy"]}/></button>
                        </CopyToClipboard>
                    </td>
                    <td>{i+1}</td>
                </tr>
            })}
        </tbody>
    </table>
}
