// import React from 'react'
import { BigNumber } from "@ethersproject/bignumber"

export function formatBalance(balance: BigNumber, decimals: number) {
    const b1 = balance.div(BigNumber.from(10).pow(decimals))
    const b2 = balance.div(BigNumber.from(10).pow(decimals-3)).mod(1000)
    const r = b1.toNumber().toLocaleString()+(b2.isZero() ? "" : "."+b2.toString().padStart(3, "0"))
    return r
}

export const ZERO = BigNumber.from(0)
export const ONE = BigNumber.from(1)
export const TWO = BigNumber.from(2)
export const TEN = BigNumber.from(10)

// finds the highest significant bit of the argument
// the result is encoded as if bits were numbered from 1
// e.g. findHsb of 0 returns 0
//      findHsb of 1 returns 1
//      findHsb of 2 returns 2
//      findHsb of 4 returns 3
//      etc.
export function findHsb(n: BigNumber): number {
    let from = 0;
    let to = 256

    while(from < to) {
        const middle = (from + to) >> 1
        const mask = ONE.shl(middle).sub(ONE)

        if(n.lte(mask)) {
            to = middle
        } else {
            from = middle+1
        }
    }

    return from
}

// finds the lowest significant bit of the argument
// the result is encoded as if bits were numbered from 1
// e.g. findLsb of 0 returns 0
//      findLsb of 1 returns 1
//      findLsb of 2 returns 2
//      findLsb of 4 returns 3
//      etc.
export function findLsb(n: BigNumber): number {
    if(n.isZero()) {
        return 0
    }
    let from = 1
    let to = 256

    while(from < to) {
        const middle = (from + to) >> 1
        const mask = ONE.shl(middle).sub(ONE)

        if(n.and(mask).isZero()) {
            from = middle+1
        } else {
            to = middle
        }
    }

    return from;
}
