import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { CopyToClipboard } from 'react-copy-to-clipboard'

import IconButton from "./IconButton"

import { verifyAddress } from "../utils/hex"

import css from "./Address.module.css"

export interface AddressProps {
    children: string;
    chainId: number;
}

const Address: React.FC<AddressProps> = ({children: address, chainId}) => {
    const url = chainId === 80001 ? "http://mumbai.polygonscan.com/address" : "http://polygonscan.com/address"

    if(!address) {
        return null
    }

    address = address.trim()
    if(!address) {
        return null
    }

    const valid = verifyAddress(address)
    const shortAddress = address.substring(0, 8)+"-"+address.substring(address.length-8)
    return <span>
        <span className={css.address+(!valid?" "+css.invalid:"")}><code>{shortAddress}</code></span>
        &nbsp;
        {!valid ? <FontAwesomeIcon style={{color: "red"}} title={"Invalid Address: "+address} icon="exclamation-triangle"/>
        : <>
            <FontAwesomeIcon icon="info"/>
            &nbsp;
            {address && <CopyToClipboard text={address}>
                <span title={"Copy: "+address}>
                    <IconButton>
                        <FontAwesomeIcon icon={["far", "copy"]}/>
                    </IconButton>
                </span>
            </CopyToClipboard>}
            <IconButton url={url+"/"+address}>
                <FontAwesomeIcon title={url+"/"+address} icon="external-link-alt"/>
            </IconButton>
        </>}
    </span>
}

export default Address
