import { BigNumber } from "@ethersproject/bignumber"
import { keccak256 } from "@ethersproject/solidity"
import { random } from "sjcl"


export default function solve(
    blockNumber: BigNumber,
    challengeHash: string,
    address: string,
    solvedDifficulty: number,
    status: (trials:number, timeMs: number, solution?: BigNumber, solvedDifficulty?: number)=>void,
) {
    const one = BigNumber.from(1)

    // Generate some good random number here
    if(!random.isReady()) {
        throw new Error("RNG is not ready")
    }
    const rnd = random.randomWords(8)
    let rnds = "0x"
    for(let i = 0; i < 8; i++) {
        const r = rnd[i] >>> 0
        rnds += r.toString(16)
    }
    let nonce = BigNumber.from(rnds)
    let difficultyN = solvedDifficulty + 1
    // The solution must be <= target to pass
    let targetN = one.shl(256-difficultyN).sub(one)

    let stop = false
    let cnt = 0
    let timeMs = 0;

    function batch() {
        if(stop) {
            return
        }
        const startTimeMs = Date.now()
        for(let i = 0; i < 1000; i++) {
            cnt++
            const digest = keccak256([ "uint256", "uint256", "uint256", "uint256" ], [ nonce, BigNumber.from(challengeHash), BigNumber.from(address), BigNumber.from(address) ])
            const digestNum = BigNumber.from(digest)
            if(digestNum.lte(targetN)) {
                solvedDifficulty = difficultyN
                while(digestNum.lte(targetN)) {
                    solvedDifficulty = difficultyN
                    difficultyN++;
                    targetN = one.shl(256-difficultyN).sub(one)
                }
                status(cnt, timeMs, nonce, solvedDifficulty)
                if(stop) {
                    // Found the required solution!
                    return
                }
            }
            nonce = nonce.add(one)
            if((cnt % 1000) === 0) {
                status(cnt, timeMs)
            }
        }
        if(!stop) {
            setTimeout(batch, 0)
        }
        const endTimeMs = Date.now()
        timeMs += endTimeMs - startTimeMs
    }

    if(!stop) {
        setTimeout(batch, 0)
    }

    return () => stop = true
}
