import React from 'react'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import css from "./Metamask.module.css"

export interface MetamaskProps {
}

export const isMetamaskInstalled = () => window.ethereum && window.ethereum.isMetaMask
export const isMetamaskConnected = () => window.ethereum && window.ethereum.isConnected() && window.ethereum._state.accounts.length > 0

const handleConnect = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault()
    try {
        await window.ethereum.request({method: "eth_requestAccounts"})
    } catch(ex) {

    }
}

const MetamaskConnectButton: React.FC<MetamaskProps> = () => {
    const className = css.connect

    return <button title="Connect this dApp to the MetaMask" className={className} onClick={(e) => handleConnect(e)}>{isMetamaskConnected() ? <>&nbsp;<FontAwesomeIcon icon={["far", "hourglass"]}/>&nbsp;</> : "Connect"}</button>
}

export default MetamaskConnectButton
