import React, { useContext, useEffect, useState } from 'react'
import { LuminaTokenContext as TokenContext } from "../hardhat/SymfoniContext"
import { LuminaRecordsContext as RecordsContext } from "../hardhat/SymfoniContext"
import { LuminaTrusteeContext as TrusteeContext } from "../hardhat/SymfoniContext"
import { LuminaAdminContext as AdminContext } from "../hardhat/SymfoniContext"
import { LuminaMarketingContext as MarketingContext } from "../hardhat/SymfoniContext"
import { ProgressContractLockerContext as LockerContext } from "../hardhat/SymfoniContext"
import { BigNumber } from "@ethersproject/bignumber"
import { Web3Provider } from "@ethersproject/providers"
import { ContractTransaction } from "@ethersproject/contracts"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Countdown from 'react-countdown'

import { TOKEN_SYMBOL, CHALLENGES_LIMIT, TOKEN_DECIMALS } from "./Parameters"
import { getReasonLabel } from "./Rules"
import Address from "./Address"
import IconButton from "./IconButton"
import CommisionSetting from "./CommisionSetting"
import Difficulties from "./Difficulties"
import MetamaskConnectButton, { isMetamaskConnected } from "./MetamaskConnectButton"

import { ChallengesType, Challenges, RegisteredBalancesType } from "./Challenges"
import { formatBalance, ZERO } from "../utils/bigNumber"
import { verifyAddress, verifyHex256 } from "../utils/hex"
import Colors from "../utils/colors"

interface Props { }

const GoMiner = false
const symbol = TOKEN_SYMBOL
const tokenDecimals = TOKEN_DECIMALS

export const LuminaToken: React.FC<Props> = () => {
    const [err, setErr] = useState("")
    const [blockNumber, setBlockNumber] = useState(0)
    const [address, setAddress] = useState("")
    const [totalSupply, setTotalSupply] = useState(ZERO)
    const [balance, setBalance] = useState(ZERO)
    const [ownerAddress, setOwnerAddress] = useState("")
    const [paused, setPaused] = useState(false)
    const [challengesAllowance, setChallengesAllowance] = useState(0)
    const [useEffectCnt, setUseEffectCnt] = useState(0)
    const [txCnt, setTxCnt] = useState(0)
    const initRegisteredBalances: RegisteredBalancesType = {balances: [], blockNumbers: []}
    const [registeredBalances, setRegisteredBalances] = useState(initRegisteredBalances)
    const initChallenges: ChallengesType = {totalCnt: 0, indexes: [], blockNumbers: [], rewardUnits: [], challengeHashes: [], nexts: [], claimsCnt: [], claimed: []}
    const [basicChallenges, setBasicChallenges] = useState(initChallenges)
    const [premiumChallenges, setPremiumChallenges] = useState(initChallenges)
    const [totalLockedSupply, setTotalLockedBalance] = useState(ZERO)
    const [lockedBalance, setLockedBalance] = useState(ZERO)
    const [lockedAvailableBalance, setLockedAvailableBalance] = useState(ZERO)
    const [claimedTokens, setClaimedTokens] = useState(ZERO)
    const [availableTokens, setAvailableTokens] = useState(ZERO)
    const [network, setNetwork] = useState({chainId: 0, name: 'unknown'})
    const [timerCnt, setTimerCnt] = useState(0)
    const [demo, setDemo] = useState(false)
    const [goMiner, setGoMiner] = useState("")
    const [goMinerErr, setGoMinerErr] = useState("")
    const [commision, setCommision] = useState(0)
    const [balanceMatic, setBalanceMatic] = useState(BigNumber.from(0))

    const token = useContext(TokenContext).instance
    const records = useContext(RecordsContext).instance
    const trustee = useContext(TrusteeContext).instance
    const admin = useContext(AdminContext).instance
    const marketing = useContext(MarketingContext).instance
    const locker = useContext(LockerContext).instance

    const provider = window.ethereum ? new Web3Provider(window.ethereum) : null
    const signer = provider && provider.getSigner()

    const mobile = true

    let eerr = ""
    if(!window.ethereum) {
        eerr = "Please install the MetaMask plugin"
    }

    async function getNetwork() {
        return provider && provider.detectNetwork()
    }

    // Listen for events
    // if(records) {
    //     console.log("records on")
    //     records.on("RegisteredBalance", (wallet, blockNumber, balance) => {
    //         console.log("RegisteredBalance", wallet, blockNumber, balance)
    //     })
    //     records.on("Commision", (wallet, commisionPrc) => {
    //         console.log("Commision", wallet, commisionPrc)
    //     })
    // }

    let mounted = true
    const refresh = () => {
        const _refresh = async () => {
            const _network = await getNetwork() || network
            if(network.chainId !== _network.chainId) {
                setNetwork({chainId: _network.chainId, name: _network.name})
            }

            if(!isMetamaskConnected()) {
                return
            }

            if (!token) {
                return null
            }
            if(!records) {
                return null
            }
            if(!trustee) {
                return null
            }
            if(!admin) {
                return null
            }
            if(!marketing) {
                return null
            }
            if(!locker) {
                return null
            }

            const t = token

            try {
            const _paused = await trustee.paused()

            const _ownerAddress = await trustee.owner()

            const _registeredBalances = await records.getBalances()

            const _totalSupply = await t.totalSupply()

            const _challengesAllowance = await admin.getChallengesAllowance()

            let _address
            try {
                _address = signer ? (await signer.getAddress()).toString() : ""
            } catch(ex) {
                _address = ""
            }

            const _commision = _address ? await records.getCommision(_address) : 0

            const _balance = _address ? await t.balanceOf(_address) : ZERO
            const _balanceMatic = signer ? await signer.getBalance() : BigNumber.from(0)

            const _blockNumber = provider ? await provider.getBlockNumber() : 0

            const _claimedTokens = await trustee.getClaimedTokens()

            const _availableTokens = await t.balanceOf(trustee.address)

            if(mounted) {
                if(useEffectCnt === 0) {
                    const _demo = await trustee.DEMO()
                    setDemo(_demo)
                }
                if(
                    paused !== _paused ||
                    ownerAddress !== _ownerAddress ||
                    // registeredBalances !== _registeredBalances ||
                    address !== _address ||
                    commision !== _commision ||
                    !totalSupply.eq(_totalSupply) ||
                    _challengesAllowance !== challengesAllowance ||
                    !balance.eq(_balance) ||
                    !balanceMatic.eq(_balanceMatic) ||
                    blockNumber !== _blockNumber ||
                    !claimedTokens.eq(_claimedTokens) ||
                    !availableTokens.eq(_availableTokens)
                ) {
                    setPaused(_paused)
                    setOwnerAddress(_ownerAddress)
                    setRegisteredBalances(_registeredBalances)
                    setAddress(_address)
                    setCommision(_commision)
                    setTotalSupply(_totalSupply)
                    setChallengesAllowance(_challengesAllowance)
                    setBalance(_balance)
                    setBalanceMatic(_balanceMatic)
                    setBlockNumber(_blockNumber)
                    setClaimedTokens(_claimedTokens)
                    setAvailableTokens(_availableTokens)
                    setUseEffectCnt(useEffectCnt + 1)
                    await readAllChallenges()
                }

                const l = locker
                const _lockedBalance = _address ? await l.balanceOf(_address) : ZERO
                const _lockedAvailableBalance = _address ? await l.availableBalanceOf(_address) : ZERO
                const _totalLockedSupply = _address ? await l.totalLockedBalance() : ZERO

                if(mounted) {
                    if(
                        lockedBalance !== _lockedBalance ||
                        lockedAvailableBalance !== _lockedAvailableBalance ||
                        totalLockedSupply !== _totalLockedSupply
                    ) {
                        setLockedBalance(_lockedBalance);
                        setLockedAvailableBalance(_lockedAvailableBalance);
                        setTotalLockedBalance(_totalLockedSupply);
                    }
                }
            }
            } catch(e) {
                const ex: any = e
                const err = ex.toString()
                let msg = ex.data && ex.data.message ? ex.data.message : ex.message ? ex.message : ex.toString()
                if(err.startsWith("Error: call revert exception ")) {
                    msg = "No smart contract, please make sure MetaMask is pointing to the MATIC network"
                    msg += " " + err
                } else if(err.startsWith("Error: ")) {
                    msg = err.substring(7)
                }
                setErr(msg)
            }
        }

        const tm = setTimeout(()=>{
            if(mounted) {
                setTimerCnt(timerCnt+1)
            }
        }, 5000)

        const _cleanup = () => {
            mounted = false
            clearTimeout(tm)
        }

        _refresh()

        return _cleanup
    }

    async function runTx<T> (
        contract: T|undefined,
        txf: (t: T) => Promise<ContractTransaction>,
        f?: (t: T) => void
    ) {
        setErr("")
        try {
            const t = contract
            if (!t) {
                throw Error("Contract instance not ready")
            }
            const tx = await txf(t)
            const cr = await tx.wait()
            if(cr.confirmations <= 0) {
                throw new Error("No confirmations in contract receipt")
            }
            if(f) {
                f(t)
            }
            setTxCnt(txCnt + 1)
        } catch(e) {
            const ex: any = e
            let msg = ex.data && ex.data.message ? ex.data.message : ex.message ? ex.message : ex.toString()
            // setErr(msg)
            const err0 = "VM Exception while processing transaction: revert "
            if(msg && msg.startsWith(err0)) {
                msg = msg.substring(err0.length)
            }
            window.alert(msg)
            return false
        }
        return true
    }

    const handlePause = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        runTx(trustee, t => t.pause())
    }

    const handleUnpause = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        runTx(trustee, t => t.unpause())
    }

    const readBasicChallenges = async () => {
        if (!admin) return

        const basicCha = await admin.readChallenges(false, CHALLENGES_LIMIT)
        const {totalCnt, indexes, blockNumbers, rewardUnits, challengeHashes, nexts, claimsCnt, claimed} = basicCha
        const ch: string[] = challengeHashes.map(n => n.toHexString())
        setBasicChallenges({totalCnt, indexes, blockNumbers, rewardUnits, challengeHashes: ch, nexts, claimsCnt, claimed})
    }

    const readPremiumChallenges = async () => {
        if (!admin) return

        const premiumCha = await admin.readChallenges(true, CHALLENGES_LIMIT)
        const {totalCnt, indexes, blockNumbers, rewardUnits, challengeHashes, nexts, claimsCnt, claimed} = premiumCha
        const ch: string[] = challengeHashes.map(n => n.toHexString())
        setPremiumChallenges({totalCnt, indexes, blockNumbers, rewardUnits, challengeHashes: ch, nexts, claimsCnt, claimed})
    }

    const readAllChallenges = async () => {
        readBasicChallenges()
        readPremiumChallenges()
    }

    const handleAddChallenges = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()

        await runTx(admin, t => t.addChallenges(challengesAllowance))
        readAllChallenges()
    }

    const handleLockBalance = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        const lockerAddress: any = locker && locker.address
        await runTx(token, t => t.approve(lockerAddress, balance))
        await runTx(locker, l => l.deposit(balance))
    }

    const handleWithdrawBalance = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        await runTx(locker, l => l.withdraw(lockedAvailableBalance))
    }

    const handleRegisterBalance = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        await runTx(token, t => t.registerBalance())
    }

    const handleClearClaim = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()
        setGoMiner("")
        setGoMinerErr("")
    }

    const parseClaim = (goMiner: string) => {
        let valid
        try {
            const claim: any = JSON.parse(goMiner)
            const { blockNumber, minerAddress: miner, address: recipient, solution } = claim


            if(
                typeof(blockNumber) === "number" &&
                typeof(miner) === "string" &&
                typeof(recipient) === "string" &&
                typeof(solution) === "string" &&
                blockNumber >= 0 &&
                verifyAddress(miner) &&
                verifyAddress(recipient) &&
                verifyHex256(solution)
            ) {
                valid = true
                setGoMinerErr("")
            } else {
                valid = false
                setGoMinerErr("Invalid GoMiner Solution String")
            }
            return { valid, 
                blockNumber: BigNumber.from(blockNumber),
                miner,
                recipient,
                solution: BigNumber.from(solution),
            }
        } catch(ex) {
            if(ex instanceof Error) {
                setGoMinerErr(ex.message)
            }
            valid = false   
        }

        return { valid, blockNumber: BigNumber.from(0), miner: "", recipient: "", solution: BigNumber.from(0) }
    }

    const handleVerifyClaim = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()

        const { valid, blockNumber, miner, recipient, solution } = parseClaim(goMiner)
        if(valid) {
            const { rewardUnits, reason } = await verifyClaim(blockNumber, miner, recipient, solution)
            verifyAlert(blockNumber, reason, rewardUnits)
        } else {
            window.alert("Invalid GoMiner Solution String")
        }
    }

    const handleClaim = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        e.preventDefault()

        const { valid, blockNumber, miner, recipient, solution } = parseClaim(goMiner)
        if(valid) {
            const success = await claimReward(blockNumber, miner, recipient, solution)
            if(success) {
                setGoMiner("")
                readAllChallenges()
            }
        } else {
            window.alert("Invalid GoMiner Solution String")
        }
    }

    const handleGoMinerChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        e.preventDefault()
        setGoMiner(e.target.value.trim())
    }

    const verifyClaim = async (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => {
        if (!trustee) {
            throw new Error("Couldn't call trustee contract")
        }
        if(paused) {
            return { rewardUnits: 0, reason: 101, premium: false, rewardsCnt: 0 }
        }
        try {
            const { rewardUnits, reason, premium, rewardsCnt } = await trustee.verifyClaim(blockNumber, miner, recipient, solution)

            return { rewardUnits, reason, premium, rewardsCnt }
        } catch(ex) {
            return { rewardUnits: 0, reason: 100, premium: false, rewardsCnt: 0 }
        }
    }

    const verifyAlert = (blockNumber: BigNumber, reason: number, rewardUnits: number) => {
        if(reason === 0) {
            window.alert("Claim Verification on block #"+blockNumber.toString()+"\nStatus: SUCCESS\nReward Amount: "+rewardUnits / 1000+" LUMI")
        } else {
            window.alert("Claim Verification on block #"+blockNumber.toString()+"\nStatus: FAILED\nReason: " + getReasonLabel(reason))
        }
    }

    const claimAlert = (blockNumber: BigNumber, reason: number, rewardUnits: number) => {
        if(reason === 0) {
        } else {
            window.alert("Claim on block #"+blockNumber.toString()+"\nStatus: FAILED\nReason: " + getReasonLabel(reason))
        }
    }

    const claimReward = async (blockNumber: BigNumber, miner: string, recipient: string, solution: BigNumber) => {
        const { rewardUnits, reason, premium } = await verifyClaim(blockNumber, miner, recipient, solution)

        if(reason === 0) {
            const success = await runTx(trustee, t => t.claimReward(blockNumber, miner, recipient, solution))
            if(success) {
                if(premium) {
                    readPremiumChallenges()
                } else {
                    readBasicChallenges()
                }
            }
            return true
        }
        claimAlert(blockNumber, reason, rewardUnits)
        return false
    }

    async function handleSetCommision(commision: number) {
        if (!records) {
            throw new Error("Couldn't call records contract")
        }
        setCommision(commision)
        const success = await runTx(records, t => t.setCommision(commision))
        if(success) {
            setCommision(commision)
        } else {
            window.alert("Could not change commision")
        }
    }

    const owner = address === ownerAddress

    useEffect(refresh, [timerCnt])

    const SmallLogo = () => <img width={24} alt="" src="logo256.png"/>
    
    const chainId = network.chainId
    const networkName = chainId === 31337 ? "HARDHAT" : chainId === 80001 ? "MUMBAI": network.name.toUpperCase()
    const faucetUrl = chainId === 31337 ? "https://matic.supply/" : chainId === 80001 ? "https://faucet.polygon.technology/": "https://matic.supply/"
    const tradeUrl = chainId === 31337 ? "https://app.uniswap.org/#/pool?chain=polygon_mumbai" : chainId === 80001 ? "https://app.uniswap.org/#/pool?chain=polygon_mumbai": "https://quickswap.exchange"

    const launchDate = new Date(Date.UTC(2022, 1, 22, 22, 22, 22))
    const launchDateStr = launchDate.toString()
    const sep = launchDateStr.indexOf(":22 ")
    const launchDateStr1 = launchDateStr.substring(0, sep+3)
    const launchDateStr2 = launchDateStr.substring(sep+4)


    type CountdownRendererProps = { days: number, hours: number, minutes: number, seconds: number, completed: number }
    const countdownRenderer: any = ({ days, hours, minutes, seconds, completed }: CountdownRendererProps) => {
        if (completed) {
          // Render a completed state
          return <span>NOW!</span>;
        } else {
          // Render a countdown
          return <span>{days}d {hours.toString().padStart(2, "0")}:{minutes.toString().padStart(2, "0")}:{seconds.toString().padStart(2, "0")}</span>;
        }
    }
    return <div>
        {(err||eerr) && (err||eerr) !== "Internal JSON-RPC error." ? <h2 style={Colors.red}>ERROR: {err||eerr}</h2> : null}
        {<div style={{...Colors.red, margin: "3px"}}>
            <br/>
            <h2>THE MINING LAUNCH</h2>
            <h1><Countdown date={launchDate} renderer={countdownRenderer}/></h1>
            <h2>UTC: 22.02.2022 22:22:22
            <br/><br/>
            LOCAL: {launchDateStr1}
            <br/>
            {launchDateStr2}
            <br/>
            <br/></h2>
        </div>}
        <p/>
        {chainId === 137 ? null : <h2 style={{color: chainId === 137 ? "black" : "red"}}><span style={{fontWeight: "bold"}}>Network:</span> {networkName} ({chainId})</h2>}
        {demo ? <h2 style={{color: "red"}}>DEMO MODE</h2> : null}
        <h2>LUMINA Coins</h2>
        <p title="The address of the Lumina ERC20 coin contract">
            <span style={{fontWeight: "bold"}}>Contract:</span>
            &nbsp;
            <Address chainId={chainId}>{token ? token.address : ""}</Address>
        </p>
        {!address ? null : <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <th style={{textAlign: "right"}}>Total Supply:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(totalSupply, tokenDecimals)} {symbol}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Locked:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(totalLockedSupply, tokenDecimals)} {symbol}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Mined:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(claimedTokens, tokenDecimals)} {symbol}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Available:</th>
                <td style={{textAlign: "right"}}>
                    {formatBalance(availableTokens, tokenDecimals)} {symbol}
                </td>
            </tr>
            </tbody>
        </table>}
        <p/>
        <SmallLogo/>
        <h2>YOUR Wallet</h2>
        <p title="The adddress of your wallet, currently connected to Metamask">
            {address ? <span style={{fontWeight: "bold"}}>
                Wallet:
                &nbsp;
                <Address chainId={chainId}>{address}</Address>
            </span> : <MetamaskConnectButton/>}
        </p>
        {!address ? null : <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <th style={{textAlign: "right"}}>Balance:</th>
                <td style={{textAlign: "left", fontWeight: "bold"}}>
                    {formatBalance(balance, tokenDecimals)} {symbol}
                    &nbsp;{<IconButton url={tradeUrl}><FontAwesomeIcon icon="shopping-cart"/></IconButton>}
                </td>
                <td>
                    {balance.gte(BigNumber.from(10).pow(tokenDecimals+5)) ? <button onClick={(e) => handleLockBalance(e)}>Lock Balance</button> : null}
                    &nbsp;{balance.gt(ZERO)?<button title="Register Balance" disabled={paused} onClick={(e) => handleRegisterBalance(e)}><FontAwesomeIcon icon="share-square"/></button>:null}
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>MATIC:</th>
                <td style={{textAlign: "left"}}>
                    {formatBalance(balanceMatic, tokenDecimals)}
                    &nbsp;MATIC
                    {balanceMatic.eq(0) ? <span title={"You need MATIC to claim LUMI rewards...\nClick the link icon to open MATIC faucet at "+faucetUrl}>
                        &nbsp;
                        <FontAwesomeIcon color="red" icon="exclamation-triangle"/>
                        <IconButton url={faucetUrl}><FontAwesomeIcon  color="red" icon="external-link-alt"/></IconButton>
                    </span> : null}
                </td>
                <td>
                </td>
            </tr>
            {registeredBalances.blockNumbers.length > 0 ? <tr>
                <th style={{textAlign: "right"}}>Registered:</th>
                <td style={{textAlign: "left"}}>{formatBalance(registeredBalances.balances[0], tokenDecimals)} {symbol}</td>
                <td/>
            </tr> : null}
            {lockedBalance.gt(0) ? <tr>
                <th style={{textAlign: "right"}}>Locked:</th>
                <td style={{textAlign: "left"}}>{formatBalance(lockedBalance, tokenDecimals)} {symbol}</td>
            </tr> : null}
            {lockedAvailableBalance.gt(0) ? <tr>
                <th style={{textAlign: "right"}}>Withdrawable:</th>
                <td style={{textAlign: "left"}}>
                    {formatBalance(lockedAvailableBalance, tokenDecimals)} {symbol}
                    &nbsp;<button disabled={paused} onClick={(e) => handleWithdrawBalance(e)}>Withdraw</button>
                </td>
            </tr> : null}
            {address ? <tr title={"Portion of reward you are willing to share with any miner,\nwho would claim mine a reward for the benefit of your wallet.\n\nAllowed values: 0% or within range [10%-90%].\n\nThe default value is 22% but you can change it at any time."}>
                <th style={{textAlign: "right"}}>Commision:</th>
                <td style={{textAlign: "left"}}><CommisionSetting commision={commision} setCommision={handleSetCommision}/></td>
                <td style={{textAlign: "left"}}><FontAwesomeIcon icon="info"/></td>
            </tr> : null}
            </tbody>
        </table>}
        { !address ? null : <>
        <p/>
        <SmallLogo/>
        {demo ? <h2 style={{color: "red"}}>DEMO MODE</h2> : null}
        <h2>Premium Challenges ({premiumChallenges.blockNumbers.length}){paused || challengesAllowance <= 0 ? null : <>&nbsp;<button title="Add More Challenges" disabled={paused || challengesAllowance <= 0} onClick={(e) => handleAddChallenges(e)}><FontAwesomeIcon icon="plus"/>{challengesAllowance}</button></>}</h2>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <td>
                    <Challenges challenges={premiumChallenges} group="premium" address={address} registeredBalances={registeredBalances} demo={demo} mobile={mobile} verifyClaim={verifyClaim} claimReward={claimReward}/>
                </td>
            </tr>
            </tbody>
        </table>
        <p/>
        <SmallLogo/>
        {demo ? <h2 style={{color: "red"}}>DEMO MODE</h2> : null}
        <h2>Regular Challenges ({basicChallenges.blockNumbers.length}){paused || challengesAllowance <= 0 ? null : <>&nbsp;<button title="Add More Challenges" disabled={paused || challengesAllowance <= 0} onClick={(e) => handleAddChallenges(e)}><FontAwesomeIcon icon="plus"/>{challengesAllowance}</button></>}</h2>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <td>
                    <Challenges challenges={basicChallenges} group="basic" address={address} registeredBalances={registeredBalances} demo={demo} mobile={mobile} verifyClaim={verifyClaim} claimReward={claimReward}/>
                </td>
                </tr>
            <tr>
                <td><p/></td>
            </tr>
            </tbody>
        </table>
        <p/>
        <SmallLogo/>
        <Difficulties/>
        {GoMiner ? <>
        <p/>
        <SmallLogo/>
        <h2>Lumina Coin GoMiner</h2>
        {mobile ? null : <p>Download GoMiner
            &nbsp;
            <a href="/downloads/gominer-linux-amd64" download="gominer">
                Linux
            </a>
            &nbsp;&bull;&nbsp;
            <a href="/downloads/gominer-linux-amd64" download="gominer">
                Linux arm64
            </a>
            &nbsp;&bull;&nbsp;
            <a href="/downloads/gominer.exe" download="gominer.exe">
                Windows
            </a>
            &nbsp;&bull;&nbsp;
            <a href="/downloads/gominer-macos-amd64.exe" download="gominer">
                Mac OS
            </a>
            &nbsp;&bull;&nbsp;
            <a href="/downloads/gominer-macos-arm64.exe" download="gominer">
                Mac OS M1
            </a>
        </p>}
        <form>
            <input type="text" size={25} value={goMiner} placeholder="paste GoMiner solution here ..." onChange={e => handleGoMinerChange(e)}/>
            <button title="Clear" disabled={!goMiner} onClick={(e) => handleClearClaim(e)}><FontAwesomeIcon icon="recycle"/></button>
            <button title="Verify" disabled={!goMiner} onClick={(e) => handleVerifyClaim(e)}><FontAwesomeIcon icon="check"/></button>
            <button title="Claim" style={{color: goMiner ? "red" : ""}} disabled={!goMiner} onClick={(e) => handleClaim(e)}><FontAwesomeIcon icon="trophy"/></button>
        </form>
        {goMinerErr ? <h2 style={Colors.red}>ERROR: {goMinerErr}</h2> : null}
        </> : null }

        {registeredBalances.blockNumbers.length > 0 ? <>
        <p/>
        <SmallLogo/>
        <h2>Registered Balances</h2>
        <p/>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <thead>
                <tr>
                    <th style={{textAlign: "right"}}>Balance</th>
                    <th>Since Block</th>
                </tr>
            </thead>
            <tbody>
                {registeredBalances.balances
                .slice(0, registeredBalances.blockNumbers.length)
                .map((balance, i) => {
                    const blockNumber = registeredBalances.blockNumbers[i]
                    return <tr key={i}>
                        <td>{formatBalance(balance, tokenDecimals)} {symbol}</td>
                        <td>{blockNumber.toString()}</td>
                    </tr>
                })}
            </tbody>
        </table>
        </> : balance.gte(BigNumber.from(10).pow(tokenDecimals-3)) ? <p>Please register your current balance to be eligible for additional benefits</p> : null}
        </>}
        <p/>
        <SmallLogo/>
        <h2>Contracts</h2>
        <table style={{marginLeft: "auto", marginRight: "auto"}}>
            <tbody>
            <tr>
                <th style={{textAlign: "right"}}>Token:</th>
                <td style={{textAlign: "left"}}>
                <Address chainId={chainId}>{token ? token.address : ""}</Address>
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Records:</th>
                <td style={{textAlign: "left"}}>
                <Address chainId={chainId}>{records ? records.address : ""}</Address>
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Trustee:</th>
                <td style={{textAlign: "left"}}>
                <Address chainId={chainId}>{trustee ? trustee.address : ""}</Address>
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Admin:</th>
                <td style={{textAlign: "left"}}>
                <Address chainId={chainId}>{admin ? admin.address : ""}</Address>
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Marketing:</th>
                <td style={{textAlign: "left"}}>
                <Address chainId={chainId}>{marketing ? marketing.address : ""}</Address>
                </td>
            </tr>
            <tr>
                <th style={{textAlign: "right"}}>Locker:</th>
                <td style={{textAlign: "left"}}>
                <Address chainId={chainId}>{locker ? locker.address : ""}</Address>
                </td>
            </tr>
            </tbody>
        </table>
        {owner ? <>
            <p/>
            <SmallLogo/>
            <h2>Management</h2>
            <table style={{marginLeft: "auto", marginRight: "auto"}}>
                <thead>
                <tr>
                    <th>Paused: {paused ? "YES" : "NO"}</th>
                </tr>
                </thead>
                <tbody>
                <tr>
                    <td>
                        <button disabled={paused} onClick={(e) => handlePause(e)}>Pause</button>
                        <button disabled={!paused} onClick={(e) => handleUnpause(e)}>Unpause</button>
                    </td>
                </tr>
                </tbody>
            </table>
        </> : null }
        <p/>
        <SmallLogo/>
        {/* <h2>Debug</h2>
        <p>Block Number: {blockNumber}</p>
        <p>Refresh Cnt: {useEffectCnt}</p> */}
   </div>
}