import React from 'react'

import { isMetamaskInstalled } from "./MetamaskConnectButton"
import { LuminaToken } from "./LuminaToken"

export interface LuminaConnectProps {
}

const BigLogo = () => <img style={{ "margin": "5px" }} width={360} alt="" src="lumina-logo.png"/>

const LuminaConnect: React.FC<LuminaConnectProps> = () => {
    const installed = isMetamaskInstalled()
    return <div>
        <BigLogo/>
        <p/>
        {!installed ? <div>
            <h2>Please Install Metamask Plugin in Your Browser ...</h2>
            <h3><a href="https://metamask.io/download/" target="_blank" rel="noopener noreferrer">https://metamask.io/download/</a></h3>
        </div> : <LuminaToken/>}
    </div>
}

export default LuminaConnect
