import { BigNumber } from "@ethersproject/bignumber"
import { findLsb, ONE, TEN } from "../utils/bigNumber"

export function getRewardUnits(challengeHash: string): [number, number] {
    let n: [number, number]
    if(challengeHash.startsWith("0x22222")) {
        n = [500, 2000]
    } else if(challengeHash.startsWith("0x2222")) {
        n = [200, 500]
    } else if(challengeHash.startsWith("0x222")) {
        n = [100, 100]
    } else if(challengeHash.startsWith("0x22")) {
        n = [50, 20]
    } else if(challengeHash.startsWith("0x2")) {
        n = [20, 5]
    } else {
        n = [10, 1]
    }
    return n
}

export function getWalletDifficultyDiscount(challengeHash: string, address: string, balance: BigNumber, decimals: number): number {
    const h1 = BigNumber.from(challengeHash)
    const h2 = BigNumber.from(address)
    let o = h1.xor(h2)
    let n = findLsb(o)
    if(n === 0) {
        n = 256
    }
    n--

    const b1 = balance.div(TEN.pow(decimals))
    if(b1.gte(ONE)) {
        if(n >= 20) {
            n = 30
        } else if(n >= 10) {
            n = 20 + (n - 10)
        } else {
            n = 2 * n
        }
    } else {
        if(n > 10) {
            n = 10
        }
    }

    return n
}

export function getBalanceDifficultyDiscount(balance: BigNumber, decimals: number): number {
    const b1 = balance.div(TEN.pow(decimals))
    const b2 = balance.mod(TEN.pow(decimals)).div(TEN.pow(decimals-3))
    let discount
    if(b1.gte(ONE)) {
        if(b1.gte(BigNumber.from(1000))) {
            discount = 2 * 10 + 10
        } else if(b1.gte(BigNumber.from(500))) {
            discount = 2 * 9 + 10
        } else if(b1.gte(BigNumber.from(200))) {
            discount = 2 * 8 + 10
        } else if(b1.gte(BigNumber.from(100))) {
            discount = 2 * 7 + 10
        } else if(b1.gte(BigNumber.from(50))) {
            discount = 2 * 6 + 10
        } else if(b1.gte(BigNumber.from(20))) {
            discount = 2 * 5 + 10
        } else if(b1.gte(TEN)) {
            discount = 2 * 4 + 10
        } else if(b1.gte(BigNumber.from(5))) {
            discount = 2 * 3 + 10
        } else if(b1.gte(BigNumber.from(3))) {
            discount = 2 * 2 + 10
        } else if(b1.gte(BigNumber.from(2))) {
            discount = 2 * 1 + 10
        } else {
            discount = 2 * 0 + 10
        }
    } else {
        if(b2.gte(BigNumber.from(500))) {
            discount = 9
        } else if(b2.gte(BigNumber.from(200))) {
            discount = 8
        } else if(b2.gte(BigNumber.from(100))) {
            discount = 7
        } else if(b2.gte(BigNumber.from(50))) {
            discount = 6
        } else if(b2.gte(BigNumber.from(20))) {
            discount = 5
        } else if(b2.gte(TEN)) {
            discount = 4
        } else if(b2.gte(BigNumber.from(5))) {
            discount = 3
        } else if(b2.gte(BigNumber.from(2))) {
            discount = 2
        } else if(b2.gte(ONE)) {
            discount = 1
        } else {
            discount = 0
        }
    }
    return discount
}

export function getDifficultyLabel(difficulty: number): string {
    let s = ""

    if(difficulty <= 20) {
        s = "too easy"
    } else if(difficulty <= 30) {
        s = "easy"
    } else if(difficulty <= 40) {
        s = "medium"
    } else if(difficulty <= 50) {
        s = "hard"
    } else if(difficulty <= 60) {
        s = "very hard"
    } else if(difficulty <= 70) {
        s = "extremely hard"
    } else if(difficulty <= 80) {
        s = "almost impossible"
    } else if(difficulty <= 90) {
        s = "impossible"
    } else {
        s = "a miracle"
    } 

    return s;
}

export function getReasonLabel(reason: number) {
    let s = ""

    if(typeof(reason) === "string") {
        return reason
    }
    switch(reason) {
        case 0:
            s = "Request satisfies all criteria to claim the tokens (at this moment)"
            break
        case 1:
            s = "blockNumber does not exist or has no live challenge assigned right now"
            break
        case 2:
            s = "Invalid or weak solution"
            break
        case 3:
            s = "This challenge has already been claimed by this address"
            break
        case 4:
            s = "All available rewards have been claimed already"
            break
        case 5:
            s = "Recipient's address is not eligible for rewards, external miners can only make claims on addresses with at least 0.001 LUMI"
            break
        case 6:
            s = "recipient's address is not eligible for rewards, it is a contract that is not a Lumina Fund"
            break
        case 100:
            s = "Invalid claim string"
            break
        case 101:
            s = "The contract is paused"
            break
        default:
            s = "Unknown Reason #" + reason
            break
    }

    return s
}
