import React from 'react'

import css from './Category.module.css'

const Cat2Color = (cat: string) => {
  let color
  switch(cat) {
    case "too easy":
        color = "white" // white
        break
    case "easy":
      color = "#ffff9c" // yellow
      break
    case "medium":
      color = "#ffcf9c" // orange
      break
    case "hard":
      color = "pink" // "#ff1010"
      break
      case "very hard":
        color = "#ff9c9c" // red
        break
      case "extremely hard":
        color = "#E1BEE7" // purple
        break
    default:
      color = "#D1C4E9" // deep purple
      break
  }
  return color
}

const Category = (props: any) => {
  const { style, cat } = props
  const color = Cat2Color(cat)

  return <span title={cat} style={{...style, backgroundColor: color}} className={css.cat}>
    &nbsp;
    {props.children}
    &nbsp;
  </span>
}

export default Category
